.mob-menu {
  z-index: 10;
  width: 250px;
  height: 100vh;
  background-color: #e17992;
  transition: transform .7s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(0);
}

